body {
  margin: 0;
  padding: 0;
  /* width: 100vw;
  height: 100vh; */
  text-align: center;
  background-color: #282c34;
 }
#keywordInput{
  margin-top: 10px;
  font-size: 24px;
  padding: 0 10px;
  line-height: 24px;
}
.gifs__container {
  margin: 25px auto 0 auto;
  display: grid;
  grid-template-columns: repeat(5, 18%);
  grid-auto-rows: 250px;
  width: 90%;
  height: 100%;
  gap: 2.8em .8em;
  place-content: center;
}
.gif__card {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: auto;
  border: 1px solid #fff;
}
.gif__card:hover {
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.603);
  border: 3px solid rgb(80, 131, 23);
}
.gif__title {
  display: grid;
  place-items: center;
  align-items: center;
  height: 10%;
  padding: 5px 10px;
  margin: 0;
  border-top: 1px solid #fff;
  font-size: calc(80%);
  letter-spacing: 1px;
  color: #fff;
  background-color: #2b2b2e;
}
.gif__image {
  width: 100%;
  height: 80%;  
}
.header__container {
  color: #fff;
  margin: 0;
  padding: 0;
}

.quantity__container {
  color: #fff;
  margin-top: 10px;
}